import authAPi from "@/api/auth.js";
import { setItem } from "@/helpers/persistanceStorage";
import axios from "axios";
import router from "@/router";

const state = {
  isLoading: false, //ожидание запроса
  token: localStorage.getItem("accessTokenStore") || "",
  isAuthenticated: false, //авторизирован ли пользователь
  validationErrors: null, //ошибки
};

export const mutationTypes = {
  loginStart: "[auth] loginStart",
  loginSuccess: "[auth] loginSuccess",
  loginFailure: "[auth] loginFailure",

  exitLoginSuccess: "[auth] exit success",
};
export const actionTypes = {
  login: "[auth] login",
  exitLogin: "[auth] exit",
};
export const gettersTypes = {
  done: "done",
};

const mutations = {
  [mutationTypes.loginStart](state) {
    state.isAuthenticated = false;
    state.validationErrors = null;
  },
  [mutationTypes.loginSuccess](state, token) {
    state.isAuthenticated = true;
    state.token = token;
  },
  [mutationTypes.loginFailure](state, payload) {
    state.isAuthenticated = false;
    state.validationErrors = payload;
  },
  [mutationTypes.exitLoginSuccess](state) {
    state.token = "";
    state.isAuthenticated = true;
  },
};
const actions = {
  [actionTypes.exitLogin](context) {
    context.commit(mutationTypes.exitLoginSuccess);
    localStorage.removeItem("accessTokenStore");
    delete axios.defaults.headers.common["Authorization"];
    router.push({ name: "login" });
  },
  [actionTypes.login](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.loginStart);
      authAPi
        .login(credentials)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            const token = response.data.result.token;
            window.localStorage.setItem(
              "accessTokenStore",
              response.data.result.token
            );
            setItem("accessTokenStore", response.data.result.token);
            context.commit(mutationTypes.loginSuccess, token);
          } else {
            context.commit(mutationTypes.loginFailure, response.data);
            localStorage.removeItem("accessTokenStore");
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.loginFailure, response.data);
          console.log("ошибка", response);
        });
    });
  },
};
export default {
  state,
  mutations,
  actions,
};
